label {
  display: block;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

input {
  padding: 8px;
  box-sizing: border-box;
  border-radius: 4px;
  border: 1px solid #dedede;

  ::placeholder {
    color: #dedede;
  }
}

textarea {
  display: block;
  width: 80%;
  margin: 1rem 0 1.5rem;
  padding: 8px;
  box-sizing: border-box;
  border-radius: 4px;
  border: 1px solid #dedede;

  ::placeholder {
    color: #dedede;
  }

  &:focus {
    outline: none;
  }
}

.formControl {

  &.error {
    input, textarea {
      border: solid 1px #f2425d;
    }
  }
}

.errorMessage {
  display: block;
  margin-top: 0.25rem;
  margin-left: 4px;
  font-size: 0.8rem;
  color: #f2425d;
}